<template>
  <div class="row">
    <div class="col">
      <b-form class="form-signin" @submit.prevent="submit">
        <div class="row">
          <div class="col-md-4 col-sm-8">
            <b-form-group label="Name">
              <b-form-input type="text" placeholder="Name" required autofocus autocomplete v-model="name" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-4 col-sm-8">
            <b-form-group label="Code">
              <b-form-input type="text" placeholder="Code" required autocomplete v-model="code" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-8">
            <b-form-group label="Description">
              <b-form-input type="text" placeholder="Description" required autocomplete v-model="description" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="Display Type">
              <b-form-select v-model="displayType" :options="list.displayTypes" value-field="displayTypeCode" text-field="name" />
            </b-form-group>
          </div>
        </div>
        <hr class="mb-4" />
        <div class="row">
          <div class="col">
            <b-button v-b-modal.typeCategoriesModal>Add Servie Type and Categories</b-button>
            <b-table striped hover small :items="typeCategories.items" :fields="typeCategories.fields">
              <template v-slot:cell(action)="row">
                <a href="#" @click.prevent="removeTypeCategory(row)">delete</a>
              </template>
            </b-table>
          </div>
        </div>
        <hr class="mb-4" />
        <div class="row">
          <div class="col">
            <b-button v-b-modal.optionsModal @click.prevent="newOption()">Add Options</b-button>
            <b-table striped hover small :items="options.items" :fields="options.fields">
              <template v-slot:cell(name)="row">
                <a href="#" v-b-modal.optionsModal @click.prevent="editOption(row)">{{ row.item.name }}</a>
              </template>
              <template v-slot:cell(action)="row">
                <a href="#" @click.prevent="removeOption(row)">delete</a>
              </template>
            </b-table>
          </div>
        </div>
        <hr class="mb-4" />
        <b-button squared block variant="dark" type="submit">Save</b-button>
      </b-form>
    </div>
    <b-modal id="optionsModal" title="Segment Options" ok-only v-if="options.current" @ok="saveOption">
      <div class="row">
        <div class="col-md-4 col-sm-8">
          <b-form-group label="Name">
            <b-form-input type="text" placeholder="Name" required autofocus autocomplete v-model="options.current.name" />
          </b-form-group>
        </div>
        <div class="col-md-4 col-sm-8">
          <b-form-group label="Code">
            <b-form-input type="text" placeholder="Code" required autocomplete v-model="options.current.code" />
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12">
          <b-form-group label="Description">
            <b-form-input type="text" placeholder="Description" required autocomplete v-model="options.current.description" />
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-md-4 col-sm-8">
          <b-form-group label="Value">
            <b-form-input type="text" placeholder="Value" required autocomplete v-model="options.current.value" />
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-md-4 col-sm-8">
          <b-form-group label="Price">
            <b-form-input type="number" placeholder="Price Order" required autocomplete v-model="options.current.priceOrder" />
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-8">
          <b-form-group label="Zones">
            <b-form-select v-model="list.zones.current" :options="availableZones" value-field="zoneId" text-field="name" />
            <b-button @click="addZone">Add Zone</b-button>
          </b-form-group>
        </div>
      </div>
      <hr class="mb-4" />
      <div class="row">
        <div class="col">
          <b-table striped hover small :items="options.current.zones" :fields="list.zones.fields">
            <template v-slot:cell(priceOrder)="row">
              <b-form-input v-model="row.item.priceOrder" />
            </template>
            <template v-slot:cell(action)="row">
              <a href="#" @click.prevent="removeZone(row)">delete</a>
            </template>
          </b-table>
        </div>
      </div>
    </b-modal>
    <b-modal id="typeCategoriesModal" title="Available Service Type and Categories" ok-only>
      <b-table striped hover small :items="availableTypeCategories" :fields="list.typeCategories.fields">
        <template v-slot:cell(action)="row">
          <a href="#" @click.prevent="addTypeCategory(row)">add</a>
        </template>
      </b-table>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: {
    segmentId: String,
  },
  computed: {
    isNew() {
      return this.segmentId == "new";
    },
    availableZones() {
      let available = this.list.zones.items.filter((x) => {
        return (
          this.options.current.zones.filter((y) => {
            return y.zoneId == x.zoneId;
          }).length == 0
        );
      });
      return available;
    },
    availableTypeCategories() {
      let available = this.list.typeCategories.items.filter((x) => {
        return (
          this.typeCategories.items.filter((y) => {
            return (
              y.serviceTypeId == x.serviceTypeId &&
              y.serviceCategoryId == x.serviceCategoryId
            );
          }).length == 0
        );
      });
      return available;
    },
  },
  async mounted() {
    await this.load();
  },
  methods: {
    async load() {
      try {
        let query = {
          includes: "filterlist.typecategories,filterlist.zones,filterlist.displaytypes",
        };
        let url = `BackendServices/Segments/${this.segmentId}`;
        let response = await this.$http.get(url, { query: query }  );

        this.name = response.name;
        this.code = response.code;
        this.description = response.description;
        this.displayType = response.displayType;
        this.typeCategories.items = response.typeCategories;
        this.options.items = response.options;

        this.list.displayTypes = response.filterList.displayTypes;
        this.list.typeCategories.items = response.filterList.typeCategories;
        this.list.zones.items = response.filterList.zones;

        this.loaded = true;
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    addTypeCategory(row) {
      let typeCategory = this.list.typeCategories.items.filter((x) => {
        return (
          x.serviceTypeId == row.item.serviceTypeId &&
          x.serviceCategoryId == row.item.serviceCategoryId
        );
      })[0];

      this.typeCategories.items.push({
        serviceTypeId: typeCategory.serviceTypeId,
        serviceTypeName: typeCategory.serviceTypeName,
        serviceCategoryId: typeCategory.serviceCategoryId,
        serviceCategoryName: typeCategory.serviceCategoryName,
      });
    },
    removeTypeCategory(row) {
      this.typeCategories.items.splice(row.index, 1);
    },
    newOption() {
      this.options.current = {
        optionId: "draft",
        name: "",
        code: "",
        description: "",
        value: "",
        priceOrder: 0,
        zones: [],
      };
    },
    editOption(row) {
      this.options.current = row.item;
    },
    saveOption() {
      if (this.options.current.optionId == "draft") {
        if (this.options.current.name && this.options.current.value) {
          this.options.current.optionId = "new";
          this.options.items.push(this.options.current);
        }
      }
    },
    removeOption(row) {
      this.options.items.splice(row.index, 1);
    },
    addZone() {
      let zone = this.list.zones.items.filter(
        (x) => x.zoneId == this.list.zones.current
      )[0];

      if (zone) {
        this.options.current.zones.push({
          zoneId: zone.zoneId,
          name: zone.name,
          priceOrder: this.options.current.priceOrder,
        });
      }

      this.list.zones.current = "";
    },
    removeZone(row) {
      this.options.current.zones.splice(row.index, 1);
    },
    async submit() {
      if (this.isNew) {
        await this.add();
      } else {
        await this.update();
      }
    },
    async add() {
      try {
        if (this.typeCategories.items.length < 1) {
          throw new Error("Types and Categories are required");
        }

        let typeCategories = this.typeCategories.items.map((x) => {
          return {
            serviceTypeId: x.serviceTypeId,
            serviceCategoryId: x.serviceCategoryId,
          };
        });

        let options = this.options.items.map((x) => {
          return {
            name: x.name,
            code: x.code,
            description: x.description,
            value: x.value,
            priceOrder: parseFloat(x.priceOrder),
            zones: x.zones.map((y) => {
              return {
                zoneId: y.zoneId,
                priceOrder: parseFloat(y.priceOrder),
              };
            }),
          };
        });

        let body = {
          name: this.name,
          code: this.code,
          description: this.description,
          displayType: this.displayType,
          typeCategories: typeCategories,
          options: options,
        };

        let url = `BackendServices/Segments`;
        await this.$http.post(url, { body });

        this.$router.push({ name: "segments" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    async update() {
      try {
        if (this.typeCategories.items.length < 1) {
          throw new Error("Types and Categories are required");
        }

        let typeCategories = this.typeCategories.items.map((x) => {
          return {
            serviceTypeId: x.serviceTypeId,
            serviceCategoryId: x.serviceCategoryId,
          };
        });

        let options = this.options.items.map((x) => {
          return {
            optionId: x.optionId,
            name: x.name,
            code: x.code,
            description: x.description,
            value: x.value,
            priceOrder: parseFloat(x.priceOrder),
            zones: x.zones.map((y) => {
              return {
                zoneId: y.zoneId,
                priceOrder: parseFloat(y.priceOrder),
              };
            }),
          };
        });

        let body = {
          name: this.name,
          code: this.code,
          description: this.description,
          displayType: this.displayType,
          typeCategories: typeCategories,
          options: options,
        };

        let url = `BackendServices/Segments/${this.segmentId}`;
        await this.$http.patch(url, { body });

        this.$router.push({ name: "segments" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
  },
  data() {
    return {
      name: "",
      code: "",
      description: "",
      displayType: "",
      typeCategories: {
        items: [],
        fields: [
          { key: "serviceTypeName", label: "Service Type" },
          { key: "serviceCategoryName", label: "Service Category" },
          { key: "action", label: "" },
        ],
      },
      options: {
        current: { zones: [] },
        items: [],
        fields: [
          { key: "name", sortable: true },
          { key: "code", sortable: true },
          { key: "priceOrder", sortable: true },
          { key: "value", sortable: true },
          { key: "action", label: "" },
        ],
      },
      list: {
        displayTypes: [],
        typeCategories: {
          items: [],
          fields: [
            { key: "serviceTypeName", label: "Service Type" },
            { key: "serviceCategoryName", label: "Service Category" },
            { key: "action", label: "" },
          ],
        },
        zones: {
          current: "",
          items: [],
          fields: [
            { key: "name" },
            { key: "priceOrder" },
            { key: "action", label: "" },
          ],
        },
      },
      loaded: false,
    };
  },
};
</script>
